/*
Top level tags, headings, etc...
 */

html {
    scroll-behavior: smooth;
}

html,
body {
    height: 100%;
    font-family: 'Roboto', sans-serif;
    font-size: 0.95em;
    background-color: #49525A!important;
    color: white;
}

hr {
    border: 0.5px solid grey;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Poppins', serif;
}

a {
    color: #23527c;
}

a:hover {
    text-decoration: none;
    color: #23527c;
}


/*
Layout-specific
 */

div.ffind-banner {
    border: 1px solid grey;
    border-radius: 5px;
    margin-top: 3rem;
    padding: 1.5rem;
}

div.card {
    background-color: #49525A!important;
    border-color: grey;
}

img.home-logo {
    width: 45%;
}

.home-card-icon {
    margin-top: 1rem;
}

.home-card-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-top: 0.5rem;
}

.home-card {
    border: 2px solid rgba(0, 0, 0, 0.125);
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.home-description {
    font-size: 1.5rem;
    margin-top: 4rem;
}

.explore-button {
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    width: 50%;
    height: 6rem;
    font-size: 2.25rem;
    font-weight: bold;
}

.wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
}

div.content {
