}


/*
Navigation
 */

.pancreatlas-nav {
    width: 100%;
    margin-bottom: 3rem;
    background-color: #1b294a;
}

.navbar-dark .navbar-brand:hover,
.navbar-dark .navbar-brand:focus,
.navbar-dark .navbar-brand {
    color: #e4e5d7 !important;
}

.navbar-dark .navbar-nav .nav-link {
    color: #e4e5d7 !important;
}


/*
Footer
 */

.footer {
    background-color: #2e2e2e;
    color: #e4e5d7;
    margin-top: 1rem;
    padding-top: 1rem;
    padding-bottom: 0;
    text-align: left;
    flex-shrink: 0;
}

.footer p {
    font-size: 14px;
}

.footer-lower {
    background-color: #262626;
}

.footer a:hover {
    color: #fff;
}

.footer a {
    color: #999;
}

.footer-img {
    max-width: 60%;
    margin-bottom: 1rem;
}


/*
Table
 */
.table { color: #fff; }

.table td {
    text-align: left;
    vertical-align: middle;
}

td.action-column {
